@import '../../stylesheets/variables';
.list {
  &__data {
    text-align: center;
  }
  &__li {
    box-shadow: 1px 1px 10px $colorGreyStone;
    background: $colorWhiteSnow;
    margin-bottom: 25px;
    text-align: left;
  }
  &__img {
    margin: 0 auto;
  }
  &__up {
    background: $colorGreyLight;
    position: relative;
    min-height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__down {
    padding: 15px;
  }
  &__id {
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    font-size: 13px;
    padding: 2px 8px;
    background: rgba(0, 0, 0, 0.3);
  }
  &__title {
    text-transform: capitalize;
    font-size: 18px;
  }
  &__types {
    margin-top: 10px;
  }
  &__types-name {
    display: inline;
    border-radius: 10px;
    border: solid 1px $colorGreyStone;
    padding: 4px 10px;
    margin-right: 10px;
    text-transform: uppercase;
    font-size: 12px;
  }
  @media all and (min-width: 768px) {
    &__data {
      // display: grid;
      // grid-template-rows: 1fr;
      // grid-template-columns: 1fr 1fr;
      // grid-gap: 25px;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
    }
    &__li {
      width: 220px;
      margin-right: 20px;
    }
  }

  // @media all and (min-width: 1000px) {
  //   // &__data {
  //   //   // grid-template-rows: 1fr;
  //   //   // grid-template-columns: 1fr 1fr 1fr 1fr;
  //   // }
  // }
}
.nouser {
  font-size: 14px;
  box-shadow: 1px 1px 10px $colorGreyStone;
  background: $colorGreyLight;
  padding: 20px;
}
